@charset "utf-8";

* {
    margin: 0;
    padding: 0;
    font-family: '돋움';
}
body, html {
    font-size: 13px;
    width: 100%;
    height: 100%;
    background-color: #f5f6f7;
    letter-spacing: -1px;
}
a {
    text-decoration: none; 
    cursor: pointer;
}
label {cursor: pointer;}

#header {
    padding-top: 60px;
    padding-bottom: 20px;    
}
#header a {
    background: url(../img/pc_sp_login_190522.png);
    width: 240px;
    height: 44px;
    display: block;
    margin: 0 auto;
}
/* form 공통 설정 */
.form_group {
    max-width: 460px;
    margin: 0 auto;
}
.join_group {
    margin-top: 20px;
    /* 마진병합 방지 */
    overflow: hidden;
}
.join_group:first-child{
    margin-top: 0;
}
h3.join_label {
    margin-top: 19px;
    margin-bottom: 8px;
}
.join_input {
    background-color: white;
    width: 100%;
    height: 51px;
    border: 1px solid #dadada;
    padding: 10px 14px;
    box-sizing: border-box;
    position: relative;
    display: inline-block;
}
.field {
    background: white;
    border: none;
    width: 100%;
    height: 29px;
    box-sizing: border-box;
    padding-right: 25px;
    font-size: 15px;
    border-radius: none;
    outline: none;
    cursor: pointer;
}
/* 선택형 공통 설정 */
.sel {
    width: 100%;
    height: 29px;
    font-size: 15px;
    border: none;
    cursor: pointer;
    outline: none;
    /* 선택버튼 이미지 변경 */
    appearance: none;
    background-image: url(../img/sel_arr_2x.gif);
    background-repeat: no-repeat;
    background-size: 20px 8px;
    background-position: 101%;
}

/* 1번 */
.id_space {
    padding-right: 110px;
}
.id_txt {
    position: absolute;
    top: 16px;
    right: 13px;
    color: #8e8e8e;
    font-size: 15px;
    letter-spacing: 0;
}

/* 2번, 3번 */
.join_input.pw_space {
    padding-right: 50px;
}
.pw_icon {
    position: absolute;
    background: url(../img/m_icon_pw_step.png);
    background-size: 125px 75px;
    width: 24px;
    height: 24px;
    right: 13px;
    top: 14px;
}
.pw_icon.lock2 {
    background-position: -27px 0;
}

/* 5번 */
.birth_wrap {
    display: flex;
}
.birth_mm, .birth_dd {
    margin-left:10px; 
}
.birth_yy, .birth_mm, .birth_dd {
    padding: 11px 14px;
    width: 147px;
    /* 총 너비 460, 여백 10*2 빼고 3등분해서 147(소수점 올림)*/
}

/* 6번, 8번 */
.gender, .country_code {
    padding-right: 10px;
}

/* 7번 */
.opt {
    font-size: 12px;
    color: #8e8e8e;
    font-weight: normal;
}

/* 9번 */
.phone_input {
    margin-top: 10px;
    position: relative;
    padding-right: 125px;
}
.telBtn {
    position: absolute;
    margin-left: 10px;
    display: inline-block;
    width: 115px;
    height: 51px;
    background-color: #03c75a;
    border: 1px solid rgba(0,0,0,.08);
}
.telBtn>a {
    color: white;
    font-size: 15px;
    padding: 18px 0;
    text-align: center;
    vertical-align: middle;
}
/* 10번 */
.auth_space {
    margin-top: 10px;
    background: #f7f7f7;
}
.auth_space>input {
    background: #f7f7f7;
}

/* 가입하기 버튼 */
.join_finish {
    background: #03c75a;
    margin-top: 30px;
    margin-bottom: 9px;
    border: 1px solid rgba(0,0,0,.08);
}
.join_finish>button {
    border: none;
    text-align: center;
    vertical-align: middle;
    color: white;
    background-color: #03c75a;
    width: 100%;
    cursor: pointer;
    font-size: 18px;
    font-weight: bold;
    padding: 15px 0;
}

/* footer */
#footer {
    margin: auto;
    max-width: 460px;
    padding: 30px 0 15px 0;
    text-align: center;
    font-style: normal;
}
.f_logo {
    position: relative;
    width: 68px;
    height: 12px;
    background: url(../img/pc_sp_login_190522.png);
    background-position: -242px 0;
    background-size: 460px;
    display: inline-block;
    text-indent: -10000px;
}
.footerInfo {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 10px;
}
.footerInfo li {
    text-decoration: none;
    list-style: none;
    color: #333;
    display: inline-block;
    text-align: center;
    padding: 0 4px;
}
.footerCopy, .footerCopy a, .footerCopy span {
    color: #333;
    font-size: 9px;
    vertical-align: middle;
}
.footerCopy {
    letter-spacing: normal;
}